import { Component, OnInit, Input, Output,  ChangeDetectionStrategy, EventEmitter } from '@angular/core';
import { ValueAccessor } from '@ionic/angular/dist/directives/control-value-accessors/value-accessor';
//import { EventEmitter } from 'protractor';
import { CountoModule }  from 'angular2-counto';

@Component({
  selector: 'app-ui-progress',
  templateUrl: './ui-progress.component.html',
  styleUrls: ['./ui-progress.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiProgressComponent implements OnInit {
  private _value = 0;
  private _valueFrom = 0.06;
  private _isFinishValueAni = false; // if progress animate finish then true
  counto = 0;
  duration = 2;
  @Input() 
  get value(){
    return this._value;
  }
  set value(val){

    this._valueFrom = this._value;
    this.valueFromChange.emit(this._valueFrom); // give 
    this._value = val;
    console.log("progress percent _value:", this._value);
    this.duration = this._value == 1 ? 0.4 : 2;
    console.log('duration,', this.duration);
    console.log('countto')
    this.valueChange.emit(this._value);
  }

  private _theme: string = "main";
  @Input()
  get theme() {
    return this._theme;
  }
  set theme(value) {
    console.log("setting color theme");
    this._theme = value;
    this.styleTheme = ' color-' + this._theme+ ' ';
  }


  @Input()
    get valueFrom(){
      return this._valueFrom;
    } 
    set valueFrom(val){
      this._valueFrom = val;
      this.valueFromChange.emit(this._valueFrom);
    }
  
  
  @Output() valueFromChange  = new EventEmitter();
  @Output() valueChange  = new EventEmitter();
  @Output() onCountoEnd = new EventEmitter();
  @Input() bgValue:number = 0;
  @Input() flagValue:number = -1;
  @Input() flagText: string = '';
  @Input() isShowValue: boolean = false;
  @Input() isShowSideValue: boolean =false;
  @Input() styleTag: string = '';
  @Input() styleSize: string = ''; // '' , lg, max
  //@Input() theme: string = 'main';
  @Input() isTextTop = false;
  @Input() textOnPercent : string;
  @Input() isStartValueZero = true;
    


  public styleSideValue: string = '';
  public styleTheme: string = "color-main";
  public styleTextTop: string; // if text on the top
  constructor() { }

  ngOnInit() {
    this.styleTag = this.isShowValue ? 'active ' : ' ';
    this.styleSideValue = this.isShowSideValue ? ' style-has-side-value ': '';
    this.styleTextTop = this.isTextTop ?  'style-has-text-top' : '';
    console.log('===== ' + this.styleTheme);
  }
  whenCountoEnd(){
    console.log('in  progress finish');
    this.onCountoEnd.emit();
  }

}
