import { Component, OnInit, Input, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-ui-form-error-msg-info',
  templateUrl: './ui-form-error-msg-info.component.html',
  styleUrls: ['./ui-form-error-msg-info.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormErrorMsgInfoComponent implements OnInit {
  @Input() styleType = "regular";
  messageShow:boolean = false;
  constructor(private changeDetecor:ChangeDetectorRef) { }

  ngOnInit() {
  }
  controlMsg(boolean){
    this.messageShow = boolean;
    this.changeDetecor.markForCheck();
  }
  hideMsg(){
    this.messageShow = false;
    this.changeDetecor.markForCheck();
  }
}
