import { Component, OnInit, Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-ui-item-option',
  templateUrl: './ui-item-option.component.html',
  styleUrls: ['./ui-item-option.component.scss']
})
export class UiItemOptionComponent implements OnInit {

  @Output() onItemOptionClick = new EventEmitter();
  

  constructor() { }

  ngOnInit() {
  }

  itemOptionClick() {
    this.onItemOptionClick.emit();
  }

}
