import { Component, OnInit, EventEmitter, Output, Inject, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-ui-item',
  templateUrl: './ui-item.component.html',
  styleUrls: ['./ui-item.component.scss']
})
export class UiItemComponent implements OnInit {
  @ViewChild('slidingList') slidingList:ElementRef;
  elementRef: ElementRef;

  @Output() onItemClick = new EventEmitter();

  async closeSlidingItems(): Promise<boolean> {
    const item = this.elementRef.nativeElement.querySelector('ion-item-sliding');
    if (item && item.closeOpened) {
      return item.closeOpened();
    }
    return false;
  }

  constructor( @Inject(ElementRef) elementRef: ElementRef) { 
    this.elementRef = elementRef;
  }

  ngOnInit() {
    this.closeSlidingItems();
  }

  itemClick() {
    this.onItemClick.emit();
    this.closeSlidingItems();
  }
 

}
