import { Component, OnInit, Input, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-ui-tab-more',
  templateUrl: './ui-tab-more.component.html',
  styleUrls: ['./ui-tab-more.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTabMoreComponent implements OnInit {
  isOpen:boolean = false;
  constructor(private changeDector:ChangeDetectorRef) { }

  ngOnInit() {
  }
  isOpenTabSelect(){
    this.isOpen=!this.isOpen;
  }
  closeTabSelect(){
    this.isOpen = false;
    this.changeDector.markForCheck();
  }
}
