import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-ui-form-checkbox2',
  templateUrl: './ui-form-checkbox2.component.html',
  styleUrls: ['./ui-form-checkbox2.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormCheckbox2Component implements OnInit {
  @Input()
  get nxValue(): any { return this.checkedValue; }
  set nxValue(newValue: any) {
    this.checkedValue = newValue;
  }
  @Output() nxValueChange = new EventEmitter();
  @Output() onClick = new EventEmitter();
  @Input() isDisable: boolean = false;
  private _isCheckSingle = false;  // if true: only one checkbox no padding; false : checkbox has padding-right
  @Input() 
  get isCheckSingle(){
    return this._isCheckSingle;
  }
  set isCheckSingle(val){
    this._isCheckSingle = val;
    this.classCheckboxSingle =  this._isCheckSingle ? 'style-single': '';
  }


  public checkedValue: boolean;
  public id: string;
  public classCheckboxSingle = '';

  get checked() {
    return this.checkedValue;
  }

  set checked(val) {
    // console.log(val);
    // console.log(typeof (val));
    this.checkedValue = val;
    if (this.nxValueChange) {
      this.nxValueChange.emit(val);
    }

    this.onClick.emit();
  }

  private generateId() {
    // console.debug('_' + Math.random().toString(36).substr(2, 12));
    return '_' + Math.random().toString(36).substr(2, 12);
  }


  constructor() {
    this.id = this.generateId();
  }

  ngOnInit() {
    this.checkedValue = this.nxValue ? true : false;
    this.classCheckboxSingle = this.isCheckSingle ? 'style-single': '';
  }

}
