import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-ui-form-error-msg-title',
  templateUrl: './ui-form-error-msg-title.component.html',
  styleUrls: ['./ui-form-error-msg-title.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormErrorMsgTitleComponent implements OnInit {
  @Input() styleType:string = "";
  @Input() showIcon:boolean = true;
  constructor() { }

  ngOnInit() {
  }
  iconControl() {
    if(this.styleType == "error"){
      return "exclamation-circle";
    }
    if(this.styleType == "info"){
      return "info-circle";
    }
    if(this.styleType == "success"){
      return "check-circle";
    }
    if(this.styleType == "warning"){
      return "exclamation-triangle"
    }
  }

}
