import { Component, OnInit, HostListener, Input, Output, EventEmitter, ViewChild, ElementRef, Inject, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { animationEnterLeavePage} from '../../model/Animation/AnimationEnterLeave';
@Component({
  selector: 'app-ui-main-side-menu',
  templateUrl: './ui-main-side-menu.component.html',
  styleUrls: ['./ui-main-side-menu.component.scss'],
  animations: [
    animationEnterLeavePage
  ]
})
export class UIMainSideMenuComponent implements OnInit {

  windowWidth: number;
  // @Input() isShow: boolean = false;
  @Input() title: string = '';

  @Input()
  get isShow() {
    return this._isShow;
  }
  set isShow(val) {
    this._isShow = val;
  } // end set isShow
  @Output() isShowChange = new EventEmitter();

  private _isShow: boolean = false;




  constructor(private changeDetector: ChangeDetectorRef) { }

  ngOnInit() {
    this.windowWidth = window.innerWidth;
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.windowWidth = event.target.innerWidth;
    // console.warn('Width: ', this.windowWidth);
  }

  // Ã¥ÂÂÃ¤Â¸ÂÃ¤Â¸ÂÃ©Â Â
  closeMain() {
    this._isShow = false;
    this.isShowChange.emit(this._isShow);
    this.changeDetector.markForCheck();
  }



}
