import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-ui-form-switcher',
  templateUrl: './ui-form-switcher.component.html',
  styleUrls: ['./ui-form-switcher.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormSwitcherComponent implements OnInit {
  @Input()
  get nxValue(): boolean {
    return this._isChecked;
  }
  set nxValue(val: boolean) {
    this._isChecked = val;
  }
  @Output() nxValueChange: EventEmitter<any> = new EventEmitter();

  @Input() labelTxt: string = '';

  constructor(private changeDector:ChangeDetectorRef) { }

  ngOnInit() {
  }

  private _isChecked: boolean = false;
  public get isChecked(): boolean {
    return this._isChecked;
  }
  public set isChecked(value: boolean) {
    this._isChecked = value;
    this.changeDector.markForCheck();
    if (this.nxValueChange)
      this.nxValueChange.emit(value);
  }


}
