import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'snd-ui-table-text-group',
  templateUrl: './ui-table-text-group.component.html',
  styleUrls: ['./ui-table-text-group.component.scss']
})
export class UiTableTextGroupComponent implements OnInit {

  @Input() titleText = "";
  @Input() subTitleText  = "";
  @Input() titleColor = 'default'; // default is color text normal, 'blue'
  @Input() isHasDot: boolean = false; // default is no dot; true is has yellow dot
  @Input() id: string = '';

  public styleTitleColor;
  public styleHasDot;

  constructor() { }

  ngOnInit() {
    this.styleTitleColor = 'style-title-color-'+ this.titleColor;
    this.styleHasDot = this.isHasDot ? 'style-has-dot' : '';
  }

}
