import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter, HostListener } from '@angular/core';

@Component({
  selector: 'app-ui-detection-scroll',
  templateUrl: './ui-detection-scroll.component.html',
  styleUrls: ['./ui-detection-scroll.component.scss']
})
export class UiDetectionScrollComponent implements OnInit {
  @ViewChild('scrollContent') scrollContent:ElementRef;
  @ViewChild('detectionContent') detectionContent:ElementRef;
  @Output() scrollEventData =  new EventEmitter();
  constructor() { }
  @Input() paddingGap = { top:10,
                          right:10,
                          bottom:10,
                          left:10}
  scrollMsg = { top:true,
                right:false,
                bottom:false,
                left:true}

  ngOnInit() {
  }
  detectionScroll(){
    let pt = this.paddingGap.top,
        pr = this.paddingGap.right,
        pb = this.paddingGap.bottom,
        pl = this.paddingGap.left;
    let SCDom = this.scrollContent.nativeElement,
        DCDom = this.detectionContent.nativeElement,
        
        SCSize = SCDom.getBoundingClientRect(),
        DCSize = DCDom.getBoundingClientRect(),

        SCWidth  = SCDom.scrollWidth,
        SCHeight = SCDom.scrollHeight,
        DCWidth  = DCSize.width,
        DCHeight = DCSize.height;
        
    this.scrollMsg.left   = DCDom.scrollLeft < pl?true:false;
    this.scrollMsg.right  = (SCWidth - (DCDom.scrollLeft+DCWidth)) < pr?true:false;
    this.scrollMsg.top    = DCDom.scrollTop < pt?true:false;
    this.scrollMsg.bottom = (SCHeight - (DCDom.scrollTop+DCHeight)) < pr?true:false;

    this.scrollEventData.emit(this.scrollMsg);
  }
  ngAfterViewInit(){
    setTimeout(()=>{
      this.detectionScroll();
    },0);
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.detectionScroll();
  }
}
