import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import { STEPSTYLETYPE } from '../../model';
// import { STEPSTYLETYPE } from './ui-rpogress-stepper-style-type-enum';

@Component({
  selector: 'app-ui-progress-stepper-style',
  templateUrl: './ui-progress-stepper-style.component.html',
  styleUrls: ['./ui-progress-stepper-style.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiProgressStepperStyleComponent implements OnInit {
  @Input() stepContral:number = 0;
  @Input() stepData:string[];
  @Input() activeStepCheck:boolean = false;
  @Input() stepType:STEPSTYLETYPE = STEPSTYLETYPE.STYLE_1;
  @Input() hasIcon:boolean = false;
  @Input() colorCode:string = '';
  constructor() { }

  ngOnInit() {
  }
  contralType(){
    if(this.stepType == STEPSTYLETYPE.STYLE_1){
      return 'step-style-1';
    }
    if(this.stepType == STEPSTYLETYPE.STYLE_2){
      return 'step-style-2';
    }
    if(this.stepType == STEPSTYLETYPE.STYLE_2_2){
      return 'step-style-2-2';
    }
  }

}
