import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { EmitterVisitorContext } from '@angular/compiler';

@Component({
  selector: 'app-ui-form-checkbox3',
  templateUrl: './ui-form-checkbox3.component.html',
  styleUrls: ['./ui-form-checkbox3.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormCheckbox3Component implements OnInit {
  @Input()
  get nxValue(): any { return this._checked; }
  set nxValue(newValue: any) {
    this._checked = newValue;
  }
  @Output() nxValueChange = new EventEmitter();
  @Output() onClick = new EventEmitter();
  @Input() isDisable: boolean = false;
  // Ã§Â¬Â¬Ã¤ÂºÂÃ§Â¨Â®Ã¦Â¨Â£Ã¥Â¼Â
  @Input() styleWeight: string = '';
  @Input() styleAlign: string = '';
  public classesStyle: string = '';
  private _checked: boolean;
  public id: string;

  get checked() {
    return this._checked;
  }

  set checked(val) {
    this._checked = val;
    if (this.nxValueChange)
      this.nxValueChange.emit(val);

      this.onClick.emit();
  }

  private generateId() {
    return '_' + Math.random().toString(36).substr(2, 12);
  }




  constructor() {
    this.id = this.generateId();
  }

  ngOnInit() {
    this.styleWeight = this.styleWeight == 'normal' ? ' font-normal' : '';
    this.styleAlign = this.styleAlign == 'end' ? ' align-end' : '';
    this.classesStyle = this.styleWeight + this.styleAlign;
  }

}
