import { Component, OnInit, Output, EventEmitter, Input, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-ui-tab-page',
  templateUrl: './ui-tab-page.component.html',
  styleUrls: ['./ui-tab-page.component.scss'],
  // changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTabPageComponent implements OnInit {
  @Input() tabSwitchFirst:string = '';
  @Input() tabSwitchSecond:string = '';
  @Input() openTab:number = 0;
  @Output() onTabChildClick = new EventEmitter();
  constructor(private changeDector:ChangeDetectorRef) { }

  ngOnInit() {
  }
  handleActive(){
    this.openTab = this.openTab?0:1;
    this.onTabChildClick.emit(this.openTab);
    this.changeDector.markForCheck();
  }
}
