import { Component, OnInit, Input, HostBinding, TemplateRef, ContentChildren, QueryList, AfterViewInit, Output, EventEmitter, ChangeDetectionStrategy,ElementRef,Renderer } from '@angular/core';
//import { UiDataGroupComponent } from './components/ui-data-group/ui-data-group.component';

@Component({
  selector: 'app-ui-form-layout-col',
  templateUrl: './ui-form-layout-col.component.html',
  styleUrls: ['./ui-form-layout-col.component.scss'],
  
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormLayoutColComponent implements OnInit {
  @Input() colPC: number = 12;
  @Input() colNB: number = 12;
  @Input() colPad: number = 12;
  @Input() colMobile: number = 12;
  @Input() hasAdd: boolean = false;
  @Input() hasRemove: boolean = false;
  @Input() translateText: string = 'Add';
  @Input() isGroupCol: boolean = false;  // col include app-group
  @Input() id: string;

  @Output() add: EventEmitter<any> = new EventEmitter();
  @Output() remove: EventEmitter<any> = new EventEmitter();
  // col control
  private classColPc: string;
  private classColNb: string;
  private classColPad: string;
  private classColMobile: string;
  private classColGroup: string;

  constructor() { }
  // render col
 @HostBinding('class') classColRender;
  // @ContentChildren(UiDataGroupComponent) btnDel: QueryList<TemplateRef<any>>;


  ngOnInit() {
    // console.log(this.btnDel);
    this.classColPc = ' gas-col-' + this.colPC;
    this.classColNb = ' gas-col-nb-' + this.colNB;
    this.classColPad = ' gas-col-pad-' + this.colPad;
    this.classColMobile = ' gas-col-mobile-' + this.colMobile;
    this.classColGroup = this.isGroupCol ? ' style-col-group' : ''
    this.classColRender = this.classColPc + this.classColNb + this.classColPad + this.classColMobile + this.classColGroup;
  }

  addHandler() {
    this.add.emit();
  }

  removeHandler() {
    this.remove.emit();
  }

}
