import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy,Renderer2,ElementRef } from '@angular/core';

@Component({
  selector: 'app-ui-btn',
  templateUrl: './ui-btn.component.html',
  styleUrls: ['./ui-btn.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiBtnComponent implements OnInit {

  @Input() btnWd: string = 'md';  //md, sm, lg
  @Input() btnHeight: string = 'default'; // default, sm
  @Input() btnStyle: string= 'full'; // full border
  @Input() btnColor: string = 'main2'; // main1, main2, light1, light2, grey
  @Input() id: string = '';
  
  
  @Input() name: string = '';
  private _isDisable: boolean = false;
  public classWd: string;
  public classHeight: string;
  public classStyle: string;
  public classDisable: string;
  public btnRenderStyle: string;
  @Output()isDisableChange = new EventEmitter();

  @Input() 
  get isDisable(){
    return this._isDisable;
  }
  set isDisable(val){
    // console.log('in set isDisable', val);
    this._isDisable = val;
    if (this.isDisableChange){
      this.isDisableChange.emit(this._isDisable);
      this.settingBtnStyle();
    }

  }

  @Output() ClickBtn = new EventEmitter<string>();

  onClickBtn(e){
    this.ClickBtn.emit(e);
  }


  public _btnGroup: string = 'group1'; //  btn Ã¦ÂÂÃ¥Â±Â¬Ã§ÂµÂÃ¥ÂÂ¥
  constructor(private el: ElementRef,private _renderer: Renderer2) {
  }
  settingBtnStyle(){
    // console.log('settingBtnStyle', this._isDisable);
    if(this.btnHeight == 'default'){
      this.classHeight = '';
      this._btnGroup = 'group1';
    }
    else{
      this.classHeight = ' btn-height-sm';
      this._btnGroup = 'group2'

    }
    this.classDisable = this.isDisable ? ' btn-disable' : ''

    this.isDisable ? this._renderer.addClass(this.el.nativeElement, 'btn-not-allow'): this._renderer.removeClass(this.el.nativeElement, 'btn-not-allow')
    
    

    
    this.classWd = ' btn-wd-'+this._btnGroup+'-'+this.btnWd;
    
    switch(this.btnStyle){
      case 'full':
        this.classStyle = ' btn-color-full-';
        break;
      case 'border':
        this.classStyle = ' btn-color-border-';
        break;
      case 'text':
        this.classStyle = ' btn-color-text-';
        break;

    }
    this.btnRenderStyle = this.classWd + this.classHeight + this.classStyle + this.btnColor + this.classDisable;
    

  }


  ngOnInit() {
    this.settingBtnStyle();

  }
    
    

}
