import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-ui-calendar-year',
  templateUrl: './ui-calendar-year.component.html',
  styleUrls: ['./ui-calendar-year.component.scss']
})
export class UiCalendarYearComponent implements OnInit {
  @Input() today: Date;
  @Input() displayMonthFormat: string;
  @Input() weekStartsOn: number = 0;
  @Input() translateMap: Map<string,string> = new Map<string, string>();

  // @Input() viewYear: number;

  private _viewYear: number;
  @Input()
  get viewYear() {
    return this._viewYear;
  }
  set viewYear(val) {
    this._viewYear = val;
    this.viewYearChange.emit(this._viewYear);

    // console.warn('ui viewYear', this._viewYear);

    if (val) {
      this.reorderWeekDays();
      this.monthInYear = this.setMonthInYear();
      // console.warn('ui monthInYear', this.monthInYear);
    }
  }
  @Output() viewYearChange = new EventEmitter();

  @Input() viewDate: Date;
  @Output() viewDateChange = new EventEmitter();

  @Output() calendarYearMonthClicked = new EventEmitter();

  weekDays: Array<string> = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  viewWeekDays: string[];
  monthInYear: Date[];

  constructor() {
  }

  ngOnInit() {
    this.reorderWeekDays();
    this.monthInYear = this.setMonthInYear();
  }

  reorderWeekDays () {

    this.viewWeekDays = this.weekDays.slice(this.weekStartsOn, this.weekDays.length);
    if (this.weekStartsOn === 0) {
      return false;
    }

    let tmp = this.weekDays.slice(0, this.weekStartsOn);
    this.viewWeekDays = this.viewWeekDays.concat(tmp);
  } // end reorderWeekDays

  setMonthInYear(): Date[] {
    let monthArr: Date[] = [];

    for (let monthVal = 0; monthVal < 12; ++monthVal) {
      monthArr.push( new Date(this.viewYear, monthVal, 1) );
    } // end for: month

    return monthArr;
  } // end setMonthInYear function

  monthClicked(date) {
    this.viewDate = date;
    this.viewDateChange.emit(this.viewDate);
    this.calendarYearMonthClicked.emit(this.viewDate);
  } // end monthClicked

}
