import { Component, OnInit, Input, ViewChild, TemplateRef,QueryList, Output, EventEmitter, AfterViewInit, ElementRef } from '@angular/core';

@Component({
  selector: 'app-ui-table-item',
  templateUrl: './ui-table-item.component.html',
  styleUrls: ['./ui-table-item.component.scss']
})
export class UiTableItemComponent implements OnInit, AfterViewInit {
  @Input() itemMinSize:string='md';
  @Input() heightLight:string='none'; // style1: blue, style2: red, style3: grey, style4: normal black
  @Input() wrap:boolean=true;
  @Input() textAlign:string='';
  @ViewChild(TemplateRef) item:TemplateRef<any>;
  @Input() sort:boolean=false;
  @Input() eventIndex:number=0;
  @Input() itemVal = '';
  @Input() id: string = '';
  public textValue:string = '';
  public elementRef: ElementRef;

  @ViewChild('tableItem') tableItem: ElementRef;

  constructor() { }
  setIndex(){
    this.eventIndex++;
    return this.eventIndex;
    
  }
  ngOnInit() {
  }
  ngAfterViewInit(){
    // console.log('in afterviewinit');
    if(this.tableItem != undefined){
      this.textValue = this.tableItem.nativeElement.textContent
    }
    // this.tableItem.forEach(element => {
    //   this.textValue = element.nativeElement.innerHTML
    // });
    // console.log('tableItem', this.tableItem);
    // console.log('this.textValue',this.textValue);
  }
}
