import { Component, OnInit, Input, AfterViewInit, ViewChild, ElementRef, Output, EventEmitter, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-ui-modal-style-feedback',
  templateUrl: './ui-modal-style-feedback.component.html',
  styleUrls: ['./ui-modal-style-feedback.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiModalStyleFeedbackComponent implements OnInit, AfterViewInit {

  @Input() hasAutoDisappear = true;

  @Input()
  get isPopupOpen() {
    return this._isPopupOpen;
  }
  set isPopupOpen(value) {
    if (this.isPopupOpen != value) {
      if (value) {
        this.autoDisappear();
        this._isPopupOpen = value;
      }
    }
  }
  @Output() isPopupOpenChange = new EventEmitter<boolean>();
  private _isPopupOpen = false;

  @ViewChild('modalBlockFeedback') modalBlockFeedback: ElementRef;

  constructor(private changeDector: ChangeDetectorRef) { }

  ngOnInit() {
    
  }

  ngAfterViewInit() {
    // window.setTimeout(() => {
    //   this.modalBlockFeedback.nativeElement.style.visibility = 'visible';
    // }, 1100);

    setInterval(() => {
      this.changeDector.markForCheck();
    },200);
  }

  autoDisappear() {
    setTimeout(() => {
      console.log(this);
      console.log('auto disapper timeout 2');
      this._isPopupOpen = false;
      this.changeDector.detectChanges();
      this.isPopupOpenChange.emit(this.isPopupOpen);
    }, 2000);
  }

}
