import { Component, OnInit, Input, Output, EventEmitter, forwardRef, ChangeDetectorRef } from '@angular/core';




@Component({
  selector: 'app-ui-form-search',
  templateUrl: './ui-form-search.component.html',
  styleUrls: ['./ui-form-search.component.scss']
})
export class UiFormSearchComponent implements OnInit {
  @Input() name: string = '';
  @Input() placeholder: string = '';
  @Input() isDisable: boolean = false;
  @Input()
  get nxValue(): any { return this.value; }
  set nxValue(newValue: any) {
    this._value = newValue;
  }
  @Output() nxValueChange: EventEmitter<any> = new EventEmitter();

  private _value: string = '';

  public get value(): string {
    return this._value;
  }
  public set value(value: string) {
    this._value = value;
    this.changeDetecor.markForCheck();
    if (this.nxValueChange)
      this.nxValueChange.emit(value);
  }

  

  constructor(private changeDetecor: ChangeDetectorRef) { }

  ngOnInit() {
  }

}
