import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-ui-link-bg',
  templateUrl: './ui-link-bg.component.html',
  styleUrls: ['./ui-link-bg.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiLinkBgComponent implements OnInit {
  @Input() text: string = '';
  @Input() textTitle: string = '';
  @Output() linkBtnClick : EventEmitter<string> = new EventEmitter<string>();
  constructor() { }

  onBtnLinkClick(textInput:string){
    this.linkBtnClick.emit(textInput);
  }

  ngOnInit() {
  }

}
