import { Component, OnInit, Output, Input, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { TABLETIELESTYLETYPE } from '../../model'
@Component({
  selector: 'app-ui-table-edit-content',
  templateUrl: './ui-table-edit-content.component.html',
  styleUrls: ['./ui-table-edit-content.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTableEditContentComponent implements OnInit {

  constructor() { }
  @Output() onClick = new EventEmitter();
  @Input() titleText = '';
  @Input() styleType:TABLETIELESTYLETYPE = TABLETIELESTYLETYPE.STYLE_1;
  @Input() event:boolean = true;

  private _isEdit = true;
  @Input() 
  set isEdit(isEdit: boolean) {
    this._isEdit = isEdit;
    this._updateStyleTableNoEdit();
  }
  get isEdit() {
    return this._isEdit;
  }

  styleTableNoEdit: string = '';

  ngOnInit() {
    this._updateStyleTableNoEdit();
  }

  btnOnClick(){
    this.onClick.emit();
  }

  private _updateStyleTableNoEdit() {
    this.styleTableNoEdit = this.isEdit ? '': 'style-table-no-edit';
  }



}
