import { Component, OnInit, Input, ViewChild, ElementRef, ChangeDetectionStrategy, Optional, HostListener } from '@angular/core';
import { Location } from '@angular/common';
import { AppRouter } from '@allianzSND/core';

@Component({
  selector: 'app-ui-inner-step',
  templateUrl: './ui-inner-step.component.html',
  styleUrls: ['./ui-inner-step.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiInnerStepComponent implements OnInit {
  @ViewChild('stepHeading') stepHeading: ElementRef;
  @ViewChild('stepMain') stepMain: ElementRef;
  @ViewChild('stepPage') stepPage: ElementRef;
  @Input() theme: string; // dark or light
  @Input() isBackShow: boolean = true; // is show back arrow
  @Input() backUrl: string = null; // back url
  public stepHeadingH = '';
  private windowHeight:number;
  private windowWidth:number;

  constructor(
    @Optional() private _location: Location,
    private router: AppRouter
  ) { }

  ngOnInit() {
    this.windowWidth = window.innerWidth;
    this.windowHeight = window.innerHeight;
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.windowWidth = event.target.innerWidth;
    this.calculateHeight();
    console.warn('resize Width: ', this.windowWidth);
  }

  closeMain() {
    if (this.backUrl) {
      this.router.navigate(this.backUrl);
    }
    else {
      if (this._location)
        this._location.back();
    }
  }
  ngAfterViewInit() {
    setTimeout(() => {
      this.stepHeadingH = window.innerWidth < 1024 ? this.stepHeading.nativeElement.offsetHeight + 'px' : '0px';
    }, 0);
  }
  ngAfterContentInit() {
    this.calculateHeight();
  }
  calculateHeight(){
    if (this.windowWidth > 1023) {
      let _this = this;
      setTimeout(function () {
        let stepPage_ele = _this.stepPage.nativeElement;
        stepPage_ele.style.height = _this.windowHeight + 'px';
        // pcLayout_ele.style.maxWidth = this.windowWidth + 'px';
      }, 100);

    }
  }

  
}
