import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Language } from '@allianzSND/core';

@Component({
  selector: 'snd-ui-table-control-bar',
  templateUrl: './ui-table-control-bar.component.html',
  styleUrls: ['./ui-table-control-bar.component.scss']
})
export class UiTableControlBarComponent implements OnInit {

  public language = new Language();
  private _isVertical = false;
  @Input() 
  get isVertical(){
    return this._isVertical;
  }
  set isVertical(val){
    this._isVertical = val;

  }
  
  @Output() onClick  =  new EventEmitter();
  

  constructor() { }


  ngOnInit() {
  }
  clickBtn(){
    this.isVertical = !this.isVertical;
    this.onClick.emit(this.isVertical);
  }

}
