
export class DateObj {

    constructor(private txt: string, private isToday: boolean) { }

    public setIsToday(isToday: boolean) {
        this.isToday = isToday;
        return this;
    }
    public getIsToday(): boolean {
        return this.isToday;
    }

    public getTxt(): string {
        return this.txt;
    }
    public setTxt(txt: string) {
        this.txt = txt;
        return this;
    }
}
