import { Directive, forwardRef, AfterContentInit, Output, EventEmitter, ContentChildren, Input, ChangeDetectorRef, QueryList } from '@angular/core';
import { UiFormRadioComponent } from '../ui-form-radio/ui-form-radio.component';


@Directive({
    selector: 'app-ui-form-radio-group',
    host: {
        '[style.display]': '"inline-block"',
        '[style.width]': '"100%"',
    }
})
export class UiFormRadioGroup implements AfterContentInit {
    private _value;
    private _selected: any = null;
    private _isInitialized: boolean = false;
    private _disabled: boolean = false;
    private _name;

    public onTouched: () => any = () => { };

    @Input()
    get nxValue(): any { return this._value; }
    set nxValue(newValue: any) {
        this.value = newValue;
        this._changeDetector.markForCheck();
    }
    @Output() nxValueChange: EventEmitter<any> = new EventEmitter();
    @ContentChildren(forwardRef(() => UiFormRadioComponent), { descendants: true })
    _radios: QueryList<UiFormRadioComponent>;


    @Output() change: EventEmitter<any> = new EventEmitter();
    @Input()
    get value(): any { return this._value; }
    set value(newValue: any) {
        if (this._value !== newValue) {
            this._value = newValue;

            this._updateSelectedRadioFromValue();
            this._checkSelectedRadioButton();
        }
    }
    @Input()
    get selected() { return this._selected; }
    set selected(selected: UiFormRadioComponent | null) {
        this._selected = selected;
        this.value = selected ? selected.value : null;
        this._checkSelectedRadioButton();
    }

    @Input()
    get disabled(): boolean { return this._disabled; }
    set disabled(value) {
        this._disabled = value;
        this._markRadiosForCheck();
    }
    get name(): string { return this._name; }
    set name(value: string) {
        this._name = value;
        this._updateRadioButtonNames();
    }

    constructor(private _changeDetector: ChangeDetectorRef) {
        this.name = this.generateId();
    }

    ngAfterContentInit() {
        this._isInitialized = true;
    }

    _checkSelectedRadioButton() {
        if (this._selected && !this._selected.checked) {
            this._selected.checked = true;
        }
    }

    private _updateSelectedRadioFromValue(): void {
        // If the value already matches the selected radio, do nothing.
        const isAlreadySelected = this._selected !== null && this._selected.value === this._value;

        if (this._radios && !isAlreadySelected) {
            this._selected = null;
            this._radios.forEach(radio => {
                radio.checked = this.value === radio.value;
                if (radio.checked) {
                    this._selected = radio;
                }
            });
        }
    }

    private _updateRadioButtonNames(): void {
        console.log('update radio names')
        console.log('this.name:', this.name);
        if (this._radios) {
            this._radios.forEach(radio => {
                radio.name = this.name;
            });
        }
    }

    _emitChangeEvent(): void {
        if (this._isInitialized) {
            this.nxValueChange.emit(this._value);
        }
    }

    onChange(val: any) {
        this._value = val;
    }

    _markRadiosForCheck() {
        if (this._radios) {
            this._radios.forEach(radio => radio._markForCheck());
        }
    }
    private generateId() {
        return '_' + Math.random().toString(36).substr(2, 12);
    }
}