import { Component, OnInit, Input, ChangeDetectionStrategy, Optional } from '@angular/core';
import { Location } from '@angular/common';

@Component({
  selector: 'app-ui-progress-circle',
  templateUrl: './ui-progress-circle.component.html',
  styleUrls: ['./ui-progress-circle.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiProgressCircleComponent implements OnInit {
  @Input() value: number = 0;
  @Input() sizeR = 60;
  @Input() sizeStroke = 5;
  constructor(@Optional() private _location: Location) { }

  ngOnInit() { }
  contentSize() {
    return (this.sizeR * 2) + this.sizeStroke + 1;
  }
  countR() {
    return (this.sizeR * 2) * Math.PI;
  }
  setNumber() {
    let circleNum = this.countR();
    let percen = Math.round(this.value * 100) / 100;
    return circleNum - (percen * circleNum);
  }
  getUrl() {
    if (this._location) {
      let urlLink = this._location.path();
      // console.log(urlLink.replace(/\//,''));
      return urlLink.replace(/\//, '');
    }
    else
      return '';
  }
}
