import { Injectable } from '@angular/core';
import { Observable, Subject, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UiInformationService {

  private _subject:Subject<string> = new Subject();
  constructor() {
    
  }

  
  // get this open info
  public getCloseAction():Observable<string>{
    console.log('_subject',this._subject);
    return this._subject.asObservable();

  }

  // 
  public closeOthers(id){
    console.log('closeOthers _subject',this._subject, id);
    this._subject.next(id);
  }



}
