import { Component, OnInit, Input, ViewChild, HostBinding, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter } from '@angular/core';
import { animationCollapse } from '../../model/Animation/AnimationCollapse';

@Component({
  selector: 'app-ui-collapse-text1',
  animations: [
    animationCollapse
  ],
  templateUrl: './ui-collapse-text1.component.html',
  styleUrls: ['./ui-collapse-text1.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiCollapseText1Component implements OnInit {
  @Input() TextType: string;
  @Input() collapseBtn: string;
  @Input() isDataEmpty: boolean;
  @Input() translateEmpty: string= "Empty";
  @Input() 
  get isCollapseBtnShow() {return this._isCollapseBtnShow;}
  set isCollapseBtnShow(isShow : boolean) {
    this._isCollapseBtnShow = isShow;
    console.debug('ui-collapse-text1',this._isCollapseBtnShow);

    this.changeDetctor.detectChanges();
  }
  private _isCollapseBtnShow : boolean = true;
  private _isOpen = false;
  @Input()
  get isOpen(){
    return this._isOpen;
  }
  set isOpen(val){
    this._isOpen = val;
    this.activeClasses = this._isOpen ? 'active' : '';
    this.isOpenChange.emit(this._isOpen);
    this.changeDetctor.detectChanges();
  }
  @Output() isOpenChange = new EventEmitter();
  public activeClasses: string = '';


  toggleCollapse() {
    this._isOpen = !this._isOpen;
    this.changeDetctor.detectChanges();
    return this._isOpen;
  }
  isOpenStatus() {
    return this._isOpen ? 'open' : 'closed';
  }

  constructor(private changeDetctor: ChangeDetectorRef) { }

  ngAfterContentInit() {
  }

  ngOnInit() {
  }

}
