import { Component, OnInit, ContentChildren, TemplateRef, QueryList, Input, Output, EventEmitter, DoCheck, ViewChild, ComponentRef, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { UiTabChildComponent } from '../ui-tab-child/ui-tab-child.component';
import { UiTabMoreComponent } from '../ui-tab-more/ui-tab-more.component';

@Component({
  selector: 'app-ui-tab-style1',
  templateUrl: './ui-tab-style1.component.html',
  styleUrls: ['./ui-tab-style1.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiTabStyle1Component implements OnInit {

  @ContentChildren(UiTabChildComponent) tabs: QueryList<TemplateRef<any>>;
  @ViewChild(UiTabMoreComponent) more_component: UiTabMoreComponent;
  @Input() idContentName: string = '';
  @Output() onTabChildClick = new EventEmitter();
  handleOpen: boolean = false;
  @Input() tabIndex = 0;
  @Input() maxShow = 4;
  constructor(private changeDector: ChangeDetectorRef) { }

  ngOnInit() {
  }


  handleClick(i) {
    let oldIndex: number = this.tabIndex;
    this.tabIndex = i;
    this.onTabChildClick.emit(i);
    this.more_component.closeTabSelect();
    this.changeDector.markForCheck();
  }
  closeSelect() {
    this.more_component.closeTabSelect();
    this.changeDector.markForCheck();
    // console.log('asasas');
  }
  getTabsArray() {
    return this.tabs.toArray();
  }




}
