import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {
  isSameDay
} from 'date-fns';

@Component({
  selector: 'app-ui-calendar-event-list',
  templateUrl: './ui-calendar-event-list.component.html',
  styleUrls: ['./ui-calendar-event-list.component.scss']
})
export class UiCalendarEventListComponent implements OnInit {

  @Input() eventList = [];
  @Input() title: string;
  @Input() colorCode: string;
  @Input() translateMap: Map<string,string> = new Map<string, string>();
  @Input() textColorStyle: string = ''; //'', textColorStyle

  @Output() clickEvent = new EventEmitter();
  styleText = '';

  constructor() { }

  ngOnInit() {
    this.styleText = this.textColorStyle == 'grey' ? ' style-grey' : '';
  }

  isSameDay(start: Date, end: Date) { // check is same day
    return isSameDay(start, end);
  }

  clickEventItem(eventItem) {
    console.warn('ui-calendar-eventList.component clickEventItem');
    this.clickEvent.emit(eventItem);
  } // end clickEventItem

}
