import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { trigger, state, style, animate, transition, } from '@angular/animations';

@Component({
  selector: 'snd-ui-collapse-content-detail',
  templateUrl: './ui-collapse-content-detail.component.html',
  styleUrls: ['./ui-collapse-content-detail.component.scss'],
  animations: [
    trigger('openClose', [
      state('*', style({
        height: '0',
      })),
      state('open', style({
        height: '*',
      })),
      state('closed', style({
        height: '0',
      })),
      transition('open <=> closed', animate('200ms ease-in-out'))
    ]),
  ],
})
export class UiCollapseContentDetailComponent implements OnInit {

  private _isOpen = false;
  @Input()
  get isOpen(){
    return this._isOpen;
  }
  set isOpen(val){
    this._isOpen = val;
    this.isOpenCange.emit(this._isOpen);
  }
  @Output() isOpenCange = new EventEmitter();

  isOpenStatus() {
    return this._isOpen ? 'open' : 'closed';
  }

  constructor() { }

  ngOnInit() {
  }

}
