import { Component, OnInit, ContentChildren, QueryList, TemplateRef, Output, Input, ElementRef, ViewChildren, EventEmitter, HostListener, forwardRef, ViewChild } from '@angular/core';
import { UiTableRowComponent } from '../ui-table-row/ui-table-row.component';
import { UiTableRowHeadComponent } from '../ui-table-row-head/ui-table-row-head.component';
import { UiDetectionScrollComponent } from '../ui-detection-scroll/ui-detection-scroll.component';

@Component({
  selector: 'app-ui-table-content',
  templateUrl: './ui-table-content.component.html',
  styleUrls: ['./ui-table-content.component.scss']
})
export class UiTableContentComponent implements OnInit {
  @ContentChildren(forwardRef(() => UiTableRowHeadComponent)) tableHead:QueryList<TemplateRef<any>>;
  //@ContentChildren(UiTableRowHeadComponent) tableHead:QueryList<TemplateRef<any>>;
  //@ContentChildren(UiTableRowComponent) tableData:QueryList<TemplateRef<any>>;
  @ContentChildren(forwardRef(() =>UiTableRowComponent)) tableData:QueryList<TemplateRef<any>>;
  
  // @ViewChild("head") head:QueryList<TemplateRef<any>>;
  @ViewChildren("head") head:QueryList<ElementRef<any>>;
  @ViewChildren("body") body:QueryList<ElementRef<any>>;
  @Output() onItemClick=new EventEmitter();
  @Output() onRowClick=new EventEmitter();
  @Output() onTableScroll=new EventEmitter();
  bodyClassName:string = "bodyClassName";
  @Input() thMinHeight:number = 30;
  @Input() tdMinHeight:number = 70;
  @Input() eventLength:number=3;  
  @Input() fixed:boolean= true;
  @Input() titleOnlyMobile:boolean = false;
  @Input() showControlbar:boolean= true;

  public showTitle:boolean = true;
  public windowWidth: number;
  bodyArrayLength:number;
  eventNum = 0;
  eventData={ index:0,
              event:0}
  rightMore:boolean = false;
  rightMoreClass = '';
  headtext = [];

  
  public elementRef: ElementRef;
  @ViewChild('headItem') headItem : ElementRef;
  public headItemData = [];

  @Input() dataHeadArray = [];
  @Input() dataRowArray = [];

  private _isShowTableVerticalView = false;
  @Input() 
  get isShowTableVerticalView(){
    return this._isShowTableVerticalView;
  }
  set isShowTableVerticalView(val:boolean){
    this._isShowTableVerticalView = val;
  }
  @Output() isShowTableVerticalViewChange = new EventEmitter();
  

  constructor(private _elementRef : ElementRef) { }

  headArray(){
    if(this.tableHead != undefined){
      let headData:Array<any>=this.tableHead.toArray();
      // console.log('tableHead',this.tableHead);
      // console.log('tableHead leng', this.tableHead.length);
      // console.log('headData',headData);
      // console.log('headData[0]',headData[0]);
      // console.log('data item2array',headData[0].itemToArray());
      if(headData[0].itemToArray() != undefined){
        return headData[0].itemToArray();
      } 
    }
  }
  bodyArray(){
    if(this.tableData != undefined){
      let bodyData:Array<any>=this.tableData.toArray();
        this.bodyArrayLength = bodyData.length;
        return bodyData;
    }
    
  }
  setHeight(){
    let td = this.body.toArray(),
        tdLength = td.length;
    let th = this.head.toArray(),
        thLength = th.length;
    let rowLength = this.tableData.toArray().length;
    for(let i = 0; i<tdLength; i++){
      td[i].nativeElement.style.height = i%rowLength?this.tdMinHeight+'px':'auto';
    }
    for(let i = 0; i<thLength; i++){
      th[i].nativeElement.style.height = i?this.thMinHeight+'px':'auto';
    }
  }

  // chagne table view
  changeTableView(isVerticalView){
    //console.log('click', isVerticalView);
    this.isShowTableVerticalView = isVerticalView;
    this.isShowTableVerticalViewChange.emit(this.isShowTableVerticalView);
  }
  
  ngOnInit() {this.windowWidth = window.innerWidth;}

  setFinalHeight(array:Array<any>,length:number,height:number){
    for(let i = 0; i < length; i++){
      array[i].style.height= height+"px";
      // console.log(array[i],height);
    }
  }
  getRowHeight(idName:String, rowLength:number){
    for(let i = 0; i < rowLength; i++){
      let domElement = this._elementRef.nativeElement.querySelectorAll(idName+""+i),
          domLength = domElement.length,
          domHArray:Array<number> = [];
      for(let j = 0; j < domLength; j++){
        domHArray.push(domElement[j].offsetHeight);
      }
      let tdHeight = Math.max.apply(null, domHArray);
      // console.log(domElement,tdHeight);
      this.setFinalHeight(domElement,domLength,tdHeight);
    }
  }
  ModifyDOMElement():void{
    let rowLength  = this.bodyArrayLength;
    let headLength = this.headArray().length;
    // console.log(rowLength,headLength);
    this.getRowHeight('.headClassName', headLength);
    this.getRowHeight('.bodyClassName', rowLength);
  }
  conrtolMoreShow(obj:UiDetectionScrollComponent['scrollMsg']){
    console.log("obj.right",obj.right);
    // this.rightMore = !obj.right;
    setTimeout( () => { this.rightMore = !obj.right; }, 0);
    // this.rightMoreClass = obj.right?'': 'rightMoreTrue';
    this.onTableScroll.emit();
  }
  setEventIndex(n){
    return n % this.eventLength;
  }
  itemClick(index:number,event:number){
    this.eventNum++;
    this.eventData.index = index;
    this.eventData.event = event % this.eventLength;
    this.onItemClick.emit(this.eventData);
  }
  rowClick(index:number){
    this.onRowClick.emit(index);
  }
  checkTitleShow(){
    if(this.titleOnlyMobile){
        setTimeout(()=>{this.showTitle = this.windowWidth<1024?true:false;},0);
    }
  }
  setTableHeight(){
    if(this.fixed){
      this.setHeight();
      this.ModifyDOMElement();
    }
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.checkTitleShow();
    this.setTableHeight();
    this.windowWidth = event.target.innerWidth;
    
  }
  ngAfterViewInit(){
    this.checkTitleShow();
    this.setHeight();

    setTimeout(()=>{
      this.setTableHeight();
    },0);
  }
}
