import { Component, ContentChildren,  QueryList, AfterContentInit, Input, ChangeDetectorRef } from '@angular/core';
import { UiFormCheckbox2Component } from '../ui-form-checkbox2/ui-form-checkbox2.component';

@Component({
  selector: 'app-ui-collapse-group',
  templateUrl: './ui-collapse-group.component.html',
  styleUrls: ['./ui-collapse-group.component.scss'],
})


export class UiCollapseGroupComponent implements AfterContentInit {
  @ContentChildren(UiFormCheckbox2Component)
  childCmps: QueryList<UiFormCheckbox2Component>;

  constructor(private changeDetector: ChangeDetectorRef) {

  }


  @Input() translateMore: string = 'more option';
  public data: object;

  get hasCollapse() {
    if (this.childCmps)
      return this.childCmps.length >= 6;
    else
      return false;
  }
  ngOnInit() {
  }
  ngAfterContentInit() {
    // console.log('ngAfterContentInit childCmps content:', this.childCmps);
    // if (this.childCmps.length >= 6) {
    //   this.hasCollapse = true;
    // }
  }
  public isOpen: boolean = false;
  public activeClasses: string = '';
  public btnDisplayClasses: string = '';
  public displayHideBlock: string = '';
  public openCloseStatus: string = 'closed';
  //isOpen = true;

  toggleCollapse() {
    this.isOpen = !this.isOpen;
    this.activeClasses = this.isOpen ? 'active' : '';
    this.btnDisplayClasses = ' hide';
    this.displayHideBlock = ' show-hide-content';
    this.openCloseStatus = this.isOpen ? 'open' : 'closed';

    this.changeDetector.markForCheck();
    this.changeDetector.detectChanges();
    return this.isOpen;
  }



}
