import { Component, OnInit, Input, Output, EventEmitter, Optional, ChangeDetectorRef, ViewChild, ElementRef, ChangeDetectionStrategy,Renderer2, AfterViewInit } from '@angular/core';
import { UiFormRadioGroup } from '../ui-form-radio-group/ui-form-radio-group.directive';

@Component({
  selector: 'app-ui-form-radio',
  templateUrl: './ui-form-radio.component.html',
  styleUrls: ['./ui-form-radio.component.scss']
})
export class UiFormRadioComponent implements OnInit, AfterViewInit {
  protected _checked: boolean = false;
  private _disabled: boolean;
  private _value: any = null;
  public id: string;
  protected radioGroup: UiFormRadioGroup;
  name: string = '';

  @ViewChild('tagBlock') tagBlock: ElementRef;


  @Input() isCheck: boolean = false;
  @Input() isDisable: boolean = false;
  @Input() colorCode: string = '';
  @Input() isAllowCancel: boolean = true; //for cancel check ( false: can't cancel check )

  @Output() change: EventEmitter<any> = new EventEmitter();
  @Input()
  get checked(): boolean { return this._checked; }
  set checked(value: boolean) {
    const newCheckedState = value;
    if (this._checked !== newCheckedState) {
      

      this.isCheck = true;
      this._checked = newCheckedState;
      if (newCheckedState && this.radioGroup && this.radioGroup.value !== this.value) {

        this.radioGroup.selected = this;

      } else if (!newCheckedState && this.radioGroup && this.radioGroup.value === this.value) {
        // When unchecking the selected radio button, update the selected radio
        // property on the group.
        this.radioGroup.selected = null;
      }

    }
    else {
      this.isCheck = false;
    }
    this.colorSetting();
    this._changeDetector.markForCheck();
  }

  @Input()
  get value(): any { return this._value; }
  set value(value: any) {
    if (this._value !== value) {
      this._value = value;
      if (this.radioGroup !== null) {
        if (!this.checked) {
          // Update checked when the value changed to match the radio group's value
          this.checked = this.radioGroup.value === value;

        }
        if (this.checked) {
          this.radioGroup.selected = this;

        }
      }
    }
  }

  @Input()
  get disabled(): boolean {
    return this._disabled || (this.radioGroup !== null && this.radioGroup.disabled);
  }
  set disabled(value: boolean) {
    const newDisabledState = value;
    if (this._disabled !== newDisabledState) {
      this._disabled = newDisabledState;
      this._changeDetector.markForCheck();
    }
  }





  constructor(
    @Optional() radioGroup: UiFormRadioGroup, 
    protected _changeDetector: ChangeDetectorRef,
    private _renderer: Renderer2) {
    this.radioGroup = radioGroup;
    this.id = this.generateId();
  }

  ngOnInit() {
    if (this.radioGroup) {
      console.log('in radio group');
      // If the radio is inside a radio group, determine if it should be checked
      this.checked = this.radioGroup.value === this._value;
      this.name = this.radioGroup.name;
    }
  }

  // Ã¥Â¾ÂÃ¨ÂÂ²Ã§Â¢Â¼Ã¨Â¨Â­Ã¥Â®ÂradioÃ§ÂÂÃ©Â¡ÂÃ¨ÂÂ²
  colorSetting(){
    if(this.tagBlock){
      let tagBlock_ele = this.tagBlock.nativeElement;
      this._renderer.setStyle(tagBlock_ele, 'borderColor', this.colorCode);
      if(this._checked){
        this._renderer.setStyle(tagBlock_ele, 'backgroundColor', this.colorCode);
        this._renderer.removeStyle(this.tagBlock.nativeElement, 'color');
      }
      else{
        this._renderer.removeStyle(this.tagBlock.nativeElement, 'backgroundColor');
        this._renderer.setStyle(tagBlock_ele, 'color', this.colorCode);
        
      }
    } // end of tagBlock
  }

  ngAfterViewInit(){
    console.log('color code:',this.tagBlock,this.colorCode);
    this.colorSetting();  
  }

  _markForCheck() {
    this._changeDetector.markForCheck();
  }

  _onInputChange(event: Event) {
    event.stopPropagation();

    const groupValueChanged = this.radioGroup && this.value !== this.radioGroup.value;
    this.checked = true;
    this._emitChangeEvent();

    if (this.radioGroup) {
      this.radioGroup.onChange(this.value);
      if (groupValueChanged) {
        this.radioGroup._emitChangeEvent();
      }
      else{ // click again to cancel checked
        if(this.isAllowCancel){
          this.checked = false;
          this.radioGroup.value = null;  
          this.radioGroup._emitChangeEvent();
        }
      }
    }
  }

  private _emitChangeEvent(): void {
    this.change.emit(this._value);
  }

  private generateId() {
    return '_' + Math.random().toString(36).substr(2, 12);
  }

}
