import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import {StringUtils} from '@allianzSND/core';

@Component({
  selector: 'snd-ui-form-textarea',
  templateUrl: './ui-form-textarea.component.html',
  styleUrls: ['./ui-form-textarea.component.scss']
})
export class UiFormTextareaComponent implements OnInit {
  @Input() maxLength:number;
  @Input() value: any;
  @Input() placeholder: string = '';
  @Output() onKeypress: EventEmitter<any> = new EventEmitter();
  @Input() height: string;
  @Input() id: string;

  @Input()
  get nxValue(): any { return this.value; }
  set nxValue(newValue: any) {
    this.value = newValue;
  }
  @Output() nxValueChange: EventEmitter<any> = new EventEmitter();


  get result() { //Ã¥ÂÂÃ¥Â¾ÂNgModelÃ§ÂÂÃ¥ÂÂ¼
    return this.value;
  }

  set result(value) { //Ã¥Â¯Â«Ã©ÂÂ²NgModelÃ§ÂÂÃ¥ÂÂ¼
    this.value = value;
    console.debug('ui-form-input',value);
    if (this.nxValueChange) {
      this.nxValueChange.emit(value);
    }
      
  }

  constructor() { }

  ngOnInit() {
  }  
  
  //keypress
  onKeyDown(event) {
    if(event.keyCode != 37 && event.keyCode != 38 && event.keyCode != 39 && event.keyCode != 40 && event.keyCode != 8) {
      if(StringUtils.byteLength(event.target.value.toString()) > (this.maxLength - 1)) {
        return false;
      }
    }
    this.onKeypress.emit(event)
  }

  onKeyUp(event) {
    let returnString = this.value;
    if (event.keyCode == 13) {
      while (StringUtils.byteLength(returnString) > (this.maxLength)) {
        returnString = returnString.substr(0, returnString.length - 1);
      }
    }
    this.value = returnString;
    this.onKeypress.emit(event)
  }

}
