import { Injectable } from "@angular/core";
import { UiModalBaseComponent } from "../ui-modal-base/ui-modal-base.component";
import { BehaviorSubject, Subject, Observable } from "rxjs";

@Injectable({
    providedIn: 'root'
})
export class ModalManager {

    constructor() { }

    private modalList: Array<{ id: string, instance: UiModalBaseComponent }> = [];
    private isGlobalLoadingOpen: boolean = false;
    private globalLoadingSubject: Subject<boolean> = new BehaviorSubject(this.isGlobalLoadingOpen);

    public pushModal(id: string, instance: UiModalBaseComponent): void {
        if (this.modalList.filter(x => x.id === id).length == 0) {
            this.modalList.push({ id, instance });
        }
    }

    public dismissModal(id: string): boolean {
        this.modalList = this.modalList.filter(x => x.id != id);
        return this.modalList.length === 0;
    }

    public closeAll() {
        console.log("ModalManager closeAll:", this.modalList);
        this.modalList.forEach(x => {
            x.instance.closeHandler();
            x.instance.ModalCloseGlobalControl();
        })
        this.modalList = [];
    }

    public toggleLoading(open: boolean) {
        this.isGlobalLoadingOpen = open;
        this.globalLoadingSubject.next(this.isGlobalLoadingOpen);
    }

    public getLoadingStatus(): Observable<boolean> {
        return this.globalLoadingSubject.asObservable();
    }
}