import { Component, OnInit, Output, Input, EventEmitter, ViewChild, ElementRef, Inject, ChangeDetectionStrategy, ChangeDetectorRef, OnDestroy, AfterViewInit, AfterContentChecked } from '@angular/core';
import { UiModalBaseComponent } from '../ui-modal-base/ui-modal-base.component';
import { ModalManager } from '../ui-modal-control/modal-manager.service';
import { animationModalOpen, animationModalClose} from '../../model/Animation/AnimationModal';

@Component({
  selector: 'app-ui-modal-style-img-base',
  templateUrl: './ui-modal-style-img-base.component.html',
  styleUrls: ['./ui-modal-style-img-base.component.scss'],
  animations: [
    animationModalOpen, animationModalClose
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiModalStyleImgBaseComponent extends UiModalBaseComponent implements OnInit,AfterViewInit, AfterContentChecked, OnDestroy {
  @Output() isPopupOpenChange = new EventEmitter();
  @Output() close = new EventEmitter();


  @Input() modalId: string;
  @Input() typeBtn: string = 'style1';  // style1 == normal -- padding-bot tom: 30px , style2 == with grey btn - padding- btm 18px , style3 == no padding - customize
  @Input() isHasBtmBlock: boolean = true;
  @Input() isBackdropClose: boolean = true;  // is background click can close the popup

  public classModalOpen: string;
  public classBtnFix: string = '';
  public classBtmBlock: string;
  public classContentFixStyle: string = '';
  public isTooHeight: boolean = false;
  public modalHeight;



  private _isPopupOpen = false;

  @Input()
  get isPopupOpen() {
    return this._isPopupOpen;
  }
  set isPopupOpen(val) {

    this._isPopupOpen = val;
    this.isPopupOpenChange.emit(this._isPopupOpen);
    // let body fixed and stop scroll if popup open
    this.renderBtmSpace();
    this.checkModalOpenStatus(this._isPopupOpen);
    this.detectScroll(this);
    this.scrollContentToTop();
    


    if (this._isPopupOpen) {
        this.changeDector.markForCheck();
    }
    

  } // end set isPopupOpen
  // all modal 
  @ViewChild('modalBlock') modalBlock: ElementRef;
  // content modal
  @ViewChild('contentBlock') contentBlock: ElementRef;






  constructor(
    protected changeDector: ChangeDetectorRef,
    protected elementRef: ElementRef,
    protected modalManager: ModalManager) {
    super(modalManager, changeDector);
  }
  ngAfterViewInit() {
    this.renderModalFixedStyle();
    this.detectScroll(this);
  }

  ngAfterContentChecked() {
    
    this.renderModalFixedStyle();
  }


  scrollContentToTop(){
    let textContentBlock = document.body.getElementsByClassName('modal-content-block');
    setTimeout(() => {
      // let content area which has scroll back to the top 
      [].forEach.bind(textContentBlock, function (textContent) {
        textContent.scrollTop = 0;
      })();
    }, 500)  // end of settimeout

  }

  
  renderModalFixedStyle(){
      this.modalHeight =  this.elementRef.nativeElement.querySelector('.modal-block').offsetHeight;
      this.classBtnFix = this.modalHeight >= 600 ? 'btn-style-fixed' : '';
      this.classContentFixStyle = this.modalHeight >= 600 ? 'style-fixed' : '';
      this.classModalFix =  this.modalHeight >= 600 ? 'style-modal-fixed' : '';
      this.changeDector.markForCheck();
  }

  contentToTop(event) {
    let element = event.target;
    if (element != undefined) element.scrollTop = 0;
  }


  ngOnInit() {
    super.ngOnInit();
    this.isHasBtmBlock ? this.classBtmBlock = '' : this.classBtmBlock = ' style-no-btm';
    this.classMobileBtnNoFix = this.isMobileBtmBtnFix ? '' : 'style-mobile-btn-no-fix';
    this.contentBlock.nativeElement.addEventListener('touchmove', function (event) {
      //event.preventDefault();
      event.stopPropagation();
    }, { passive: false });
  }
  ngOnDestroy() {
    super.ngOnDestroy();
  }
  isOpenStatus() {
    return this.isPopupOpen ? 'open' : 'closed';

  }
  isBgOpenStatus() {
    return this.isPopupOpen ? 'bgOpen' : 'bgClosed';
  }
  closeHandler() {
    this.isPopupOpen = false;
    this.close.emit(false);
  }
  backdropClose() {
    if (this.isBackdropClose) {
      this.closeHandler();
    }
  }
  //detect if content area scroll to bottom add a class in modalblock
  @Output() onScrollContentBtm = new EventEmitter();
  detectScroll(event) {
    
   // console.log('in modal detetect scroll event', event);
    let element = event.target;
    let atBottom = false
    if (element != undefined) {
      atBottom = element.scrollHeight - element.scrollTop <= (element.clientHeight + 50);
     // console.log('if atBottom', atBottom);
    } else {
      atBottom = false;
    }
    console.log('detect scroll atBottom', atBottom);
    this.onScrollContentBtm.emit(atBottom);

    if (atBottom) {
      //console.log('if atBottom', atBottom);
      if (this.modalBlock != null) {
        this.modalBlock.nativeElement.classList.add('style-scroll-on-btm');
      }
    }

  }

}
