import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
//import { EventEmitter } from 'protractor';

@Component({
  selector: 'app-ui-link',
  templateUrl: './ui-link.component.html',
  styleUrls: ['./ui-link.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiLinkComponent implements OnInit {
  @Input() imgSrc: string;
  @Input() isUnderLine: boolean = true;
  @Input() isHasImg: boolean = true;
  @Input() isImgFront = false;
  @Input() isReset = false;
  @Input() id:string = '';
  classImgFront = '';

  

  @Output() onClick = new EventEmitter();
  onClickLink(){
    this.onClick.emit();
  }
 
  classLinkStyle: string;
  constructor() { }

  ngOnInit() {
    this.classImgFront = this.isImgFront? ' type-img-front': '';
    this.classLinkStyle = this.isUnderLine ? ' ' : ' type-no-underline';
  }

}
