import { Component, AfterViewInit, Input, Output, ViewChild, ElementRef, EventEmitter, Renderer2, ChangeDetectionStrategy } from '@angular/core';
import { UiFormCheckbox2Component } from '../ui-form-checkbox2/ui-form-checkbox2.component';


@Component({
  selector: 'app-ui-form-checkbox',
  templateUrl: './ui-form-checkbox.component.html',
  styleUrls: ['./ui-form-checkbox.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormCheckboxComponent extends UiFormCheckbox2Component implements AfterViewInit{

  @Input() colorCode: string = '';
  @ViewChild('checkBlock') checkBlock: ElementRef;
  constructor(private _renderer: Renderer2) {
    super();
  }
  

  
  // Ã¥Â¾ÂÃ¨ÂÂ²Ã§Â¢Â¼Ã¨Â¨Â­Ã¥Â®ÂcheckboxÃ§ÂÂÃ©Â¡ÂÃ¨ÂÂ²
  colorSetting(){
    let checkblock_ele = this.checkBlock.nativeElement;
    this._renderer.setStyle(checkblock_ele, 'borderColor', this.colorCode);
    if(this.nxValue){
      this._renderer.setStyle(checkblock_ele, 'backgroundColor', this.colorCode);
    }
    else{
      this._renderer.removeStyle(this.checkBlock.nativeElement, 'backgroundColor');
    }

  }

  ngAfterViewInit(){
    //console.log('color code:',this.checkBlock,this.colorCode);
    this.colorSetting();  
  }
  get nxValue(): any { return this.checkedValue; }
  set nxValue(newValue: any) {
    this.colorSetting();
    this.checkedValue = newValue;
  }
  
  

}
