import { Component, OnInit, HostListener, Input, EventEmitter, Output, ChangeDetectionStrategy, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { UiModalBaseComponent } from '../ui-modal-base/ui-modal-base.component';
import { ModalManager } from '../ui-modal-control/modal-manager.service';

@Component({
  selector: 'app-ui-modal-confirm',
  templateUrl: './ui-modal-confirm.component.html',
  styleUrls: ['./ui-modal-confirm.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class UiModalConfirmComponent extends UiModalBaseComponent implements OnInit, OnDestroy {

  @Input() title: string;
  @Input() message: string;
  @Input() cancelBtnText = 'Cancel';
  @Input() confirmBtnText = 'Confirm';
  @Input() backdropDismiss = false;
  @Input() hasCancelBtn = true;
  @Input() hasConfirmBtn = true;

  private _isPopupOpen = false;
  @Input()
  get isPopupOpen() {
    return this._isPopupOpen;
  }
  set isPopupOpen(val) {
    this._isPopupOpen = val;
    this.checkModalOpenStatus(this._isPopupOpen);
    if (val) {
      this.presentAlertConfirm();
    }
  }

  @Output() isPopupOpenChange = new EventEmitter();

  @Output() onCancel = new EventEmitter();
  @Output() onConfirm = new EventEmitter();

  buttons: Array<any> = [];

  alert = null;


  constructor(
    public alertController: AlertController,
    private ModalManager: ModalManager,
    private changeDetector: ChangeDetectorRef
  ) {
    super(ModalManager, changeDetector);
  }

  async presentAlertConfirm() {
    this.alert = await this.alertController.create({
      header: this.title,
      message: this.message,
      backdropDismiss: this.backdropDismiss,
      buttons: this.buttons
    });

    await this.alert.present();
  } // end presentAlertConfirm function

  // @HostListener('click', ['$event'])



  ngOnInit() {
    super.ngOnInit();
    if (this.hasCancelBtn) {
      this.buttons.push({
        text: this.cancelBtnText,
        role: 'cancel',
        cssClass: 'secondary',
        handler: () => {
          this.onCancel.emit();
          this.isPopupOpenChange.emit(false);
        }
      });
    } // end if: hasCancelBtn

    if (this.hasConfirmBtn) {
      this.buttons.push({
        text: this.confirmBtnText,
        handler: () => {
          // console.log('Confirm:', this.confirmBtnText);
          this.onConfirm.emit();
          this.isPopupOpenChange.emit(false);
        }
      });
    } // end if: hasConfirmBtn
  } // end ngOnInit

  ngOnDestroy(){
    super.ngOnDestroy();
  }

  closeHandler() {
    this._isPopupOpen = false;
    this.isPopupOpenChange.emit(false);
    this.alert.dismiss();
  }

}
