import { Component, OnInit, ContentChildren, QueryList, TemplateRef, HostListener, Input, ChangeDetectionStrategy } from '@angular/core';
import { ListItemComponent } from './list-item/list-item.component';
import { TABLELISTSTYLETYPE } from '../../model';
@Component({
  selector: 'app-ui-table-list',
  templateUrl: './ui-table-list.component.html',
  styleUrls: ['./ui-table-list.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTableListComponent implements OnInit {
  @ContentChildren(ListItemComponent) items:QueryList<TemplateRef<any>>
  @Input() styleType:TABLELISTSTYLETYPE = TABLELISTSTYLETYPE.STYLE_1;
  windowWidth: number;
  constructor() { }
  ngOnInit() {
    this.windowWidth = window.innerWidth;
  }
  
  infoListArray(){
    return this.items.toArray();
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.windowWidth = event.target.innerWidth;
  }
  controlStyle(){
    if(this.styleType == TABLELISTSTYLETYPE.STYLE_1){
      return '';
    }
    if(this.styleType == TABLELISTSTYLETYPE.STYLE_2){
      return "style-2";
    }
  }

}
