import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';
import { CONTENTGAP } from '../../model';

@Component({
  selector: 'app-ui-content-b-gap',
  templateUrl: './ui-content-b-gap.component.html',
  styleUrls: ['./ui-content-b-gap.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiContentBGapComponent implements OnInit {
  @Input() gapSize:CONTENTGAP = CONTENTGAP.GAP20;
  constructor() { }

  
  handleGap(type:CONTENTGAP){
    if(type == CONTENTGAP.GAP20){
      return 'contentGap20';
    }
    if(type == CONTENTGAP.GAP40){
      return 'contentGap40';
    }
  }
  ngOnInit() {
  }
}
