import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { animationCollapse } from '../../model/Animation/AnimationCollapse';
@Component({
  selector: 'app-ui-table-collapse-content',
  animations: [
    animationCollapse
  ],
  templateUrl: './ui-table-collapse-content.component.html',
  styleUrls: ['./ui-table-collapse-content.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiCollapseContentComponent implements OnInit {

  constructor(private changeDector:ChangeDetectorRef) { }
  @Input() isShow:boolean = true;
  @Input() isOpen: boolean = true;
  @Input() isHasCollapse: boolean = true;
  @Input() titleText:string = '';
  @Output() onCollapsing = new EventEmitter();
  public styleNoCollapse : string;
  ngOnInit() {
    this.styleNoCollapse = this.isHasCollapse ? '' : 'style-no-collapse';
  }

  toggleCollapse() {
    this.isOpen = !this.isOpen;
    this.onCollapsing.emit(this.isOpen);
    this.changeDector.markForCheck();
    this.changeDector.detectChanges();
    return this.isOpen;
  }
  isOpenStatus(){
    return this.isOpen ? 'open' : 'closed';
  }

}
