import { Component, OnInit, Input , Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-ui-btn-like-add',
  templateUrl: './ui-btn-like-add.component.html',
  styleUrls: ['./ui-btn-like-add.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiBtnLikeAddComponent implements OnInit {

  @Input() 
  get isLike() {return this._isLike;}
  set isLike(isLike : boolean) {
    this._isLike = isLike;

    // console.debug('like-component',this._isLike);
    this.changeClass();
    
  }  
  private _isLike: boolean;

  @Input() isDisable: boolean = false;
  @Output() onChange: EventEmitter<boolean> = new EventEmitter();
  public likeClasses: string;
  public disableClasses: string;
  public totalClasses: string;

  constructor() { }

  ngOnInit() {
    this.changeClass();
  }

  changeClass() {
    
    this.likeClasses = this._isLike ? ' active' : ' ';
    this.disableClasses = this.isDisable ? ' disabled' : ' ';
    this.totalClasses = this.likeClasses + this.disableClasses;

    // console.debug('totalClasses',this.totalClasses);
  }

  toggleLike(){
    this._isLike = ! this._isLike;
    this.changeClass();
    this.onChange.emit(this._isLike);
    
    return false;
  }
  



}
