import { Component, OnInit, Input, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-ui-title-style1',
  templateUrl: './ui-title-style1.component.html',
  styleUrls: ['./ui-title-style1.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTitleStyle1Component implements OnInit {
  @Input() isShowOther: boolean = false;
  @Input() otherEleStatus: string = '';
  @Input() isFullCol: boolean = true; // true: title nowrap; false: title wrap
  constructor() { }

  ngOnInit() {
    this.otherEleStatus = this.isShowOther ? ' active': '';
  }

}
