import { Component, OnInit, ContentChildren, QueryList, TemplateRef, ChangeDetectionStrategy } from '@angular/core';
import { UiTabStyle1Component } from '../ui-tab-style1/ui-tab-style1.component';
import { UiTabMoreComponent } from '../ui-tab-more/ui-tab-more.component';

import { UiTabStyle2Component } from '../ui-tab-style2/ui-tab-style2.component';
import { UiTabChildComponent } from '../ui-tab-child/ui-tab-child.component';

@Component({
  selector: 'app-ui-title-tab',
  templateUrl: './ui-title-tab.component.html',
  styleUrls: ['./ui-title-tab.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTitleTabComponent implements OnInit {
  @ContentChildren(UiTabStyle1Component) tabs1: QueryList<UiTabStyle1Component>;
  // @ContentChildren(UiTabChildComponent) tab1: QueryList<UiTabChildComponent>;
  // @ContentChildren(UiTabChildComponent) tab1: QueryList<TemplateRef<any>>;
  @ContentChildren(UiTabStyle2Component) tabs2: QueryList<UiTabStyle2Component>;

  constructor() { }

  ngOnInit() {
  }
  tabLength(){
    // console.log(this.tabs1,this.tabs2);
    if(this.tabs1.length != 0 || this.tabs2.length != 0){
      if(this.tabs1.length != 0){
        return this.tabs1.first.tabs.length;
      }
      else{
        // console.log(this.tabs2);
        return this.tabs2.first.tabs.length;
      }
    }
    else{
      //todo throw error
      console.warn('You can only put <app-ui-tab-style1 tabInfo> or <app-ui-tab-style2 tabInfo> in it.');
    }
    
  }
}
