
export class MainMenuOption {

  constructor(private link: string, private name: string, private code: string, private imgSrc: string) { }


  public getCode() {
    return this.code;
  }
  public setLink(link: string) {
    this.link = link;
    return this;
  }
  public getLink(): string {
    return this.link;
  }

  public setName(name: string) {
    this.name = name;
    return this;
  }
  public getName(): string {
    return this.name;
  }

  public setImgSrc(imgSrc: string) {
    this.imgSrc = imgSrc;
    return this;
  }
  public getImgSrc(): string {
    return this.imgSrc;
  }
}
