import { Component, OnInit, Input, Output, EventEmitter, HostListener, forwardRef, SimpleChanges, OnChanges, ChangeDetectionStrategy } from '@angular/core';
import { SelectOption } from '@allianzSND/core';
import { UiFormErrorMsgInfoComponent } from '../ui-form-error-msg-info/ui-form-error-msg-info.component';



@Component({
  selector: 'app-ui-form-select',
  templateUrl: './ui-form-select.component.html',
  styleUrls: ['./ui-form-select.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiFormSelectComponent implements OnInit {

  @Input()
  get nxValue(): any { return this._value; }
  set nxValue(newValue: any) {
    this._value = newValue;
    this.titleFloat();
  }
  @Output() nxValueChange: EventEmitter<any> = new EventEmitter();
  @Output() onChange = new EventEmitter();

  @Input() selectTitle: string;  //Ã¤Â¸ÂÃ¦ÂÂÃ©ÂÂ¸Ã¥ÂÂ®Ã¦Â¨ÂÃ©Â¡Â
  @Input() selectOption: Array<SelectOption>;  //Ã¤Â¸ÂÃ¦ÂÂÃ©ÂÂ¸Ã¥ÂÂ®Ã©ÂÂ¸Ã©Â Â
  
  @Input() placeholder: string = 'Select';
  @Input() isShowTitle: boolean = true;
  @Input() isShowDefaultOption: boolean = true;
  @Input() isNoSpace: boolean = false;
  public dropdownGroup: string;
  public styleTitleFloatNow = ''; // title float or keep;
  public styleNoSpace = ''
  private _value = '';

  // error status style

  public classError : string = '';
  public classDisable: string = ''
  public inputStatus: boolean = false;

  @Input() 
  get isError(): boolean {return this.inputStatus;}
  set isError(status:boolean){
    
    this.inputStatus = status;
    this.classError = status ? ' style-error': '';
  }

  @Output() isErrorChange: EventEmitter<any> = new EventEmitter();

  private _isDisable: boolean = false;
  @Input() 
  get isDisable(){
    return this._isDisable;
  }
  set isDisable(val){
    this._isDisable = val;
    this.classDisable = this._isDisable ?  'style-disable': ''; 
  }

  
  get currentValue() {
    return this._value;
  }

  set currentValue(val) {
    this._value = val;
    if (this.nxValueChange)
      this.nxValueChange.emit(val);
  }

  @Output('focus') onFocus = new EventEmitter();
  @Output('blur') onBlur = new EventEmitter();
  @Input() errorMagInfo: UiFormErrorMsgInfoComponent;

  isFocus: boolean = false;

  focusBlurEvent(boolean: boolean) {
    if (this.errorMagInfo != undefined) {
      this.errorMagInfo.controlMsg(this.isFocus);
    }
  }

  selectChange(val){
    //console.log(val);
    this.titleFloat();
    this.onChange.emit(val);
    // this.nxValueChange.emit(val);

  }
  
  // when open the select
  focus() {
    this.isFocus = true;
    this.onFocus.emit();
    this.focusBlurEvent(this.isFocus);
    // when open the page stop page scroll 
    // console.log('select open');
    this.titleFloat();
    
    // // in every form-stop-scroll class add no-scroll
    // let stopFormScrollBlock = document.body.getElementsByClassName('form-stop-scroll');
    // [].forEach.bind(stopFormScrollBlock,function(itm){
    //   itm.classList.add('all-no-scroll');
    // })();

  }
  //when close the select
  blur() {
    this.isFocus = false;
    this.onBlur.emit();
    this.focusBlurEvent(this.isFocus);
    // in every form-stop-scroll class add no-scroll
    // let stopFormScrollBlock = document.body.getElementsByClassName('form-stop-scroll');
    // [].forEach.bind(stopFormScrollBlock,function(itm){
    //   itm.classList.remove('all-no-scroll');
    // })();
  }
  //control title float
  titleFloat(){
    // console.log('select nxValue',this.nxValue);
    if( this.nxValue === undefined || this.nxValue === null || this.nxValue === ''){
      this.styleTitleFloatNow = '';
    }
    else {
      this.styleTitleFloatNow = ' style-title-floating';
    }
  }

  ngOnInit() {
    // this.titleFloat();
    // this.styleNoSpace = this.isNoSpace ? 'no-space': '';
    this.classDisable = this._isDisable ?  'style-disable': ''; 
  }

  constructor() { }

  


}
