import { Component, OnInit,Input,Output,EventEmitter, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'snd-ui-modal-msg',
  templateUrl: './ui-modal-msg.component.html',
  styleUrls: ['./ui-modal-msg.component.scss']
})
export class UiModalMsgComponent implements OnInit {
  
  constructor(private changeDetector: ChangeDetectorRef) { }
  public alertInit:boolean = false;
  private _isPopupOpen = false;
  
  @Input()
  get isPopupOpen() {
    return this._isPopupOpen;
  }
  set isPopupOpen(val) {
    if(this.isPopupOpen != val && val){
      this.alertInit=true; 
      this.autoDisappear();
      this._isPopupOpen = val;
    }
  }

  @Output() isPopupOpenChange = new EventEmitter();
  ngOnInit() {
  }

  autoDisappear() {
    setTimeout(() => {
      this._isPopupOpen = false;
      this.isPopupOpenChange.emit(this.isPopupOpen);
      setTimeout(() => {
        this.alertInit=false; 
        this.changeDetector.detectChanges();
      },2000)
    }, 1600);
  }



}
