import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { TABLETIELESTYLETYPE } from '../../model';

@Component({
  selector: 'app-ui-table-title',
  templateUrl: './ui-table-title.component.html',
  styleUrls: ['./ui-table-title.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiTableTitleComponent implements OnInit {
  @Input() titleText='';
  @Input() subTitleText='';
  @Input() event:boolean=true;
  @Input() eventIcon:string='';
  @Output() onClick = new EventEmitter();
  @Input() styleType:TABLETIELESTYLETYPE = TABLETIELESTYLETYPE.STYLE_1;
  @Input() isShowTitle = true;
  constructor() { }

  ngOnInit() {
  }
  click(){
    this.onClick.emit();
  }
  controlStyle(){
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_1){
      return 'style-1';
    }
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_2){
      return "style-2";
    }
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_3){
      return "style-3";
    }
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_4){
      return "style-4";
    }
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_5){
      return "style-5";
    }
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_6){
      return "style-6";
    }
    if(this.styleType == TABLETIELESTYLETYPE.STYLE_7){
      return "style-7";
    }
  }
}
