import { Component, OnInit, Input, ViewChild, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import {trigger,state,style,animate,transition,} from '@angular/animations';

@Component({
  selector: 'app-ui-collapse-card1',
  animations: [
    trigger('openClose', [
      state('open', style({
        height: '*',
        opacity: 1
      })),
      state('closed', style({
        height: '0',
        opacity: 0
      })),
      transition('open => closed', animate('200ms ease-in-out')),
      transition('closed => open', animate('200ms ease-in-out'))
    ]),
  ],
  templateUrl: './ui-collapse-card1.component.html',
  styleUrls: ['./ui-collapse-card1.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiCollapseCard1Component implements OnInit {
  @Input() tagText: string;
  @Input() TextType: string;
  @Input() collapseBtn: string;
  @Input() isDataEmpty: boolean;
  @Input() isHide : boolean;
  @Input() emptyText: Array<string>= ['You can click', ' to add your customer\'s information.'];
  constructor(private changeDetector:ChangeDetectorRef) { }

  ngOnInit() {
  }
  public isOpen: boolean = false;
  public activeClasses: string= '';

  //isOpen = true;

  toggleCollapse() {
    this.isOpen = !this.isOpen;
    this.activeClasses = this.isOpen ? 'active' : '';
    this.changeDetector.markForCheck();
    this.changeDetector.detectChanges();
    return this.isOpen;
  }
  isOpenStatus(){
    return this.isOpen ? 'open' : 'closed';
  }
  @ViewChild('collapseContentHeight') collapseContentHeight:ElementRef;

  ngAfterContentInit(){
    //console.log('content child:', this.collapseContentHeight.nativeElement.childNodes.length);
    // has one if hide html, so child > 1 show no other child
   // this.collapseContentHeight.nativeElement.childNodes.length > 1 ? this.isHide = false : this.isHide = true;
  }

  

}
