import { Component, OnInit, ViewChild, ElementRef, Inject } from '@angular/core';

@Component({
  selector: 'app-ui-item-sliding',
  templateUrl: './ui-item-sliding.component.html',
  styleUrls: ['./ui-item-sliding.component.scss']
})
export class UiItemSlidingComponent implements OnInit {

  elementRef: ElementRef;
  
  async closeSlidingItems(): Promise<boolean> {
    const item = this.elementRef.nativeElement.querySelector('ion-item-sliding');
    if (item && item.closeOpened) {
      return item.closeOpened();
    }
    return false;
  }
  
 

  constructor( @Inject(ElementRef) elementRef: ElementRef) { 
    this.elementRef = elementRef;
    this.closeSlidingItems();
  }


  ngOnInit() {
    this.closeSlidingItems();
  }
  

}
