import { Component, OnInit, Output, EventEmitter, Input, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-ui-main-full',
  templateUrl: './ui-main-full.component.html',
  styleUrls: ['./ui-main-full.component.scss'],
  changeDetection:ChangeDetectionStrategy.OnPush
})
export class UiMainFullComponent implements OnInit {
  @Input()
  public get isLoadingFinish() {
    return this._isLoadingFinish;
  }
  public set isLoadingFinish(value) {
    this._isLoadingFinish = value;
    this.isLoadingFinishChange.emit(this._isLoadingFinish);
  }
  @Output() isLoadingFinishChange = new EventEmitter();

  @Output() refreshCallback = new EventEmitter();

  public _isLoadingFinish: boolean = false;

  constructor(private changeDector:ChangeDetectorRef) { }

  ngOnInit() {
  }

  // loading content
  refreshContent(event) {
    this.isLoadingFinish = false;
    this.refreshCallback.emit(event);
    this.changeDector.markForCheck();
  } // end loadContent
}
